<template>
	<div v-if="attachments.length > 0">
	<Fancybox
      :options="{
		idle: false,
		//compact: false,
		dragToClose: false,
		showClass: 'f-fadeIn',
		hideClass: 'f-fadeOut',
		Images: {
			zoom: false,
		},
		Thumbs: {
			type: 'classic',
		},
        Carousel: {
          infinite: false,
        },
		Toolbar: {
			display: {
				left: [],
				middle: [],
				right: ['download', 'close'],
			},
		},
		contentClick: () => false,
      }"
    >
	<div  v-for="attachment in attachments" :key="attachment.file_id">
		<a :href="aaoChatFileServerURL+'/download/attachment/'+attachment.file_id+'?authKey='+authKey" data-fancybox :data-download-src="aaoChatFileServerURL+'/download/attachment/'+attachment.file_id+'?authKey='+authKey+'&download=true'" class="aaochat-image-preview chat-attachment-box" v-if="(attachment.extension=='png'||attachment.extension=='jpg'||attachment.extension=='jpeg'||attachment.extension=='gif')">
			<div class="chat-image" >
				<img draggable="false" :src="getAttachmentThumbUrl(attachment)" style="width:100%" alt="">
			</div>
		</a>
		
		<a class="chat-attachment-box" data-fancybox data-type="iframe" data-width="640" :data-download-src="aaoChatFileServerURL+'/download/attachment/'+attachment.file_id+'?authKey='+authKey+'&download=true'" :href="aaoChatFileServerURL+'/download/attachment/'+attachment.file_id+'?authKey='+authKey" v-if="(attachment.extension=='mp4'||attachment.extension=='ogg'||attachment.extension=='webm')">
			<div class="file-icon-container">
				<img :src="aaoChatServerURL+'/public/images/'+getFileIcon(attachment)" alt="">
			</div>
			{{attachment.display_name}}
		</a>

		<div class="chat-image chat-video" v-if="(attachment.extension=='mp3'||attachment.extension=='wav'||attachment.extension=='m4a')">
			<audio controls style="">
				<source :src="getAttachmentUrl(attachment)" :type="attachment.extension=='m4a'?'audio/mp3':'audio/'+attachment.extension" />
			</audio>
		</div>

		<a class="chat-attachment-box" :href="aaoChatFileServerURL+'/download/attachment/'+attachment.file_id+'?download=true&authKey='+authKey" download
		v-if="!(attachment.extension=='mp3'||attachment.extension=='m4a'||attachment.extension=='wav'||attachment.extension=='png'||attachment.extension=='jpg'||attachment.extension=='jpeg'||attachment.extension=='gif'||attachment.extension=='mp4'||attachment.extension=='ogg'||attachment.extension=='webm')">
			<div class="file-icon-container">
				<img :src="aaoChatServerURL+'/public/images/'+getFileIcon(attachment)" alt="">
			</div>
			{{attachment.display_name}}
		</a>
	</div>
	</Fancybox>
	</div>
</template>

<script>
import Fancybox from './Fancybox.vue';

export default {
	name: 'AaochtMessageAttachment',
	components: {
		Fancybox
	},
	props: {
		/**
		 * The AaochtMessageAttachment to render.
		 */
		 attachments: {
			type: Array,
			required: true,
		},
	},
	data() {
		return {
			dateFromNow: '',
			loggedUser: OC.getCurrentUser(),
			dateInterval: 0,
			authKey: localStorage.getItem("ngStorage-AuthKey").replace(/"/g,''),
            aaoChatServerURL: localStorage.getItem("nextcloud-AaoChatServerURL"),
            aaoChatFileServerURL: localStorage.getItem("nextcloud-AaoChatFileServerURL"),
		}
	},
	computed: {
		
	},
	created() {

	},
	destroyed() {

	},
	methods: {
		getAttachmentUrl(attachment) {
			return this.aaoChatFileServerURL+'/download/attachment/'+attachment.file_id+'.'+attachment.extension+'?authKey='+this.authKey
		},
		getAttachmentThumbUrl(attachment) {
			return this.aaoChatFileServerURL+'/download/attachment/thumb/'+attachment.file_id+'.'+attachment.extension+'?authKey='+this.authKey
		},
		getFileIcon(attachment) {
			let extension = attachment.extension;
			if (['pdf'].includes(extension)) {
                return 'pdf.svg';
            } else if (['doc', 'docx', 'rtf'].includes(extension)) {
                return 'doc.svg'
            } else if (['ppt', 'pptx'].includes(extension)) {
                return 'ppt.svg'
            } else if (['xls', 'csv', 'xlsx'].includes(extension)) {
                return 'xls.svg'
            } else if (['txt'].includes(extension)) {
                return 'txt.svg'
            } else if (['mp4', 'ogg', 'webm'].includes(extension)) {
                return 'play-media.png'
            } else {
                return 'file.svg'
            }
		},
		openFancyBox (e,dataUrl) {
			const imageList = [
				{ url: dataUrl }
			];
			fancyBox(e.target,imageList);
		}
	},
}
</script>

