		margin-right: 15px;
	}

	#tab-aaochat .file-icon-container img {
		height: 30px;
		vertical-align: middle;
		border: 0;
	}
	.chat-video >>> audio {
		width: 300px;
		margin-bottom: -3px;
	}
</style>